import { ref } from 'vue';

const isPreviewVisible = ref(false);
const previewImage = ref('');
const isZoomed = ref(false);
const zoomedPosition = ref({ x: 0, y: 0 });
const dragStart = ref({ x: 0, y: 0 });
const isDragging = ref(false);
const zoomOrigin = ref({ x: 0, y: 0 });
let animationFrameId = null;

const openPreviewModal = (imageUrl) => {
  previewImage.value = imageUrl;
  isPreviewVisible.value = true;
  isZoomed.value = false;
  zoomedPosition.value = { x: 0, y: 0 };
  zoomOrigin.value = { x: 0, y: 0 };
};

const closePreviewModal = () => {
  isPreviewVisible.value = false;
  isZoomed.value = false;
  zoomedPosition.value = { x: 0, y: 0 };
  zoomOrigin.value = { x: 0, y: 0 };
};

const toggleZoom = (event) => {
  if (isDragging.value) return; // 드래그 중에는 확대/축소를 토글하지 않음

  if (!isZoomed.value) {
    isZoomed.value = true;
    const rect = event.target.getBoundingClientRect();
    zoomOrigin.value = {
      x: event.clientX - rect.left,
      y: event.clientY - rect.top,
    };
  } else {
    isZoomed.value = false;
    zoomedPosition.value = { x: 0, y: 0 };
  }
};

const startDrag = (event) => {
  if (!isZoomed.value) return;
  isDragging.value = true;

  // 터치 이벤트인지 확인하고 좌표 설정
  const clientX = event.touches ? event.touches[0].clientX : event.clientX;
  const clientY = event.touches ? event.touches[0].clientY : event.clientY;

  dragStart.value = { x: clientX, y: clientY };

  // 이벤트 유형에 따라 적절한 이벤트 등록
  window.addEventListener(event.type === 'touchstart' ? 'touchmove' : 'mousemove', onDrag);
  event.preventDefault();
};


const onDrag = (event) => {
  if (!isDragging.value || !isZoomed.value) return;

  // 터치 이벤트인지 확인하고 좌표 설정
  const clientX = event.touches ? event.touches[0].clientX : event.clientX;
  const clientY = event.touches ? event.touches[0].clientY : event.clientY;

  const deltaX = clientX - dragStart.value.x;
  const deltaY = clientY - dragStart.value.y;

  if (!animationFrameId) {
    animationFrameId = requestAnimationFrame(() => {
      zoomedPosition.value = {
        x: zoomedPosition.value.x + deltaX,
        y: zoomedPosition.value.y + deltaY,
      };

      dragStart.value = { x: clientX, y: clientY };
      animationFrameId = null;
    });
  }

  event.preventDefault();
};


const endDrag = (event) => {
  if (!isZoomed.value) return;
  isDragging.value = false;

  // 이벤트 유형에 따라 적절한 이벤트 제거
  window.removeEventListener(event.type === 'touchend' ? 'touchmove' : 'mousemove', onDrag);
};


// Export as a function to use in components
export function usePreviewModal() {
  return {
    isPreviewVisible,
    previewImage,
    isZoomed,
    zoomedPosition,
    zoomOrigin,
    openPreviewModal,
    closePreviewModal,
    toggleZoom,
    startDrag,
    onDrag,
    endDrag,
  };
}
